/* google fonts */

/* reset default style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* base style */

body {
  background: #fff;
  font-family: "Istok Web", sans-serif;
 }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "RocknRoll One", sans-serif !important;
}

a {
  text-decoration: none;
  color: #141414;
}
input,button,textarea{
  outline: none;
  border: none;
}
h1,
h2 {
